<template>
    <div class="searchArea">
        <form>
            <div class="searchTabs">
                <button type="button" @click="toggleDropdown">{{ selectedOptionText }}</button>
                <ul v-if="dropdownVisible" class="dropdown-menu">
                    <li @click="selectOption(null)">
                        전체
                    </li>
                    <li
                        v-for="option in options"
                        :key="option.value"
                        @click="selectOption(option)">
                        {{option.text}}
                    </li>
                </ul>
            </div>
            <div class="searchBarW">
                <div>
                    <input type="search" placeholder="찾으시는 오픈소스를 입력해주세요." class="searchBar">
                    <button class="searchBtn"><img src="@/assets/images/main/search.png" alt="검색아이콘"></button>
                </div>
            </div>
        </form>
    </div>
</template>
<script>
export default {
    name: 'MainSearchbar', 
    data() {
        return {
            selectedOption: null, 
            dropdownVisible: false, 
            options: [
                { value: 'option1', text: '테스트 1' },
                { value: 'option2', text: '테스트 2' },
                { value: 'option3', text: '테스트 3' },
            ],
        }; 
    },  
    computed: {
        selectedOptionText() {
            return this.selectedOption ? this.selectedOption.text : '전체';
        }
    }, 
    methods: {
        toggleDropdown() {
            this.dropdownVisible = !this.dropdownVisible;
        }, 
        selectOption(option) {
            this.selectedOption = option; 
            this.dropdownVisible = false; 
            this.$emit('select-change', option ? option.value : 'all');
        }
    }
}
</script>