                @close="showPopup = false"
            >
            <component :is="popupContent"></component>
        </BasicPopup>    
    </div>
</template>
<script>
import BasicPopup from '@/layouts/popup/BasicpopUp.vue'
import CommongrouppopUp from '@/components/ui/popup/CommongrouppopUp.vue'
import CommongenerationpopUp from '@/components/ui/popup/CommongenerationpopUp.vue'
import TableCount from '@/components/ui/table/TableCount.vue';
import { ref } from 'vue';
export default {
    name: 'CommonCode', 
    components: {
    TableCount,
    BasicPopup,
    CommongrouppopUp,
    CommongenerationpopUp
  },
  setup() {
    const showPopup = ref(false);
    const popupContent = ref(null);
    const popupTitle = ref('');
    const popupFooterText = ref('저장');
    const popupFooterLink = ref('/'); 


    const openCommongrouppopUp = () => {
      popupContent.value = CommongrouppopUp;
      popupTitle.value = "공통 코드 그룹 생성 팝업";
      showPopup.value = true;
    };

    const openCommongenerationpopUp = () => {
      popupContent.value = CommongenerationpopUp;
      popupTitle.value = "공통 코드 생성 팝업";
      showPopup.value = true;
    };


    return {
      openCommongrouppopUp,
      openCommongenerationpopUp,
      showPopup,
      popupContent,
      popupTitle,
      popupFooterLink,
      popupFooterText
    };
  },

}
</script>
