<template>
  <div class="FaqPage">
    <div class="tabMenu">
      <button
        v-for="tab in tabs"
        :key="tab.name"
        @click="currentTab = tab.name"
        :class="{ current: currentTab === tab.name }"
      >
        {{ tab.label }} (<span class="countcolor">{{ tab.count }}</span>)
      </button>
    </div>

    <div class="flex-end mb12">
      <SearchForm/>
    </div>

    <div class="faqContent mb30">
      <ul>
        <li v-for="(item, index) in filteredFaqItems" :key="index" :class="{ on: item.isOpen }">
          <div class="topContent" @click="toggleContent(index)">
            <span class="title">{{ item.title }}</span>
            <p class="contentP">
              <span>
                {{ item.description }}
                <span v-if="item.files.length > 0" class="downclip"></span>
              </span>
            </p>
            <span class="arrow"></span>
          </div>
          <div class="btmContent">
            <p class="contentP">{{ item.content }}</p>
            <div class="downloadW" v-if="item.files.length > 0">
              <ul>
                <li v-for="(file, fileIndex) in item.files" :key="fileIndex">
                  <a href="">
                    <span class="title">{{ file.name }}</span><span class="kb">({{ file.size }}kb)</span>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </li>
      </ul>
    </div>

    <div class="relative clearfix">
        <Pagination class="aliginCenter"/>
        <router-link to="/community/qna/QnaList" class="BtnStyle2 floatR">
              <span>Q&A 바로가기</span>
        </router-link>
    </div>
  </div>
</template>

<script>
import SearchForm from '@/components/ui/search/SearchForm.vue'
import Pagination from '@/components/ui/Pagination.vue'
import { ref, computed } from 'vue';

export default {
  name: 'ExamplePage',
  components: {
        SearchForm,
        Pagination
  },
  setup() {
    const faqItems = ref([
      {
        category: '오픈소스 다운로드',
        title: '오픈소스 다운로드',
        description: '오픈소스 다운로드 항목의 FAQ 제목 영역입니다. 관리자에서 등록합니다.',
        content: 'test입니다test입니다test입니다test입니다',
        isOpen: false,
        files: [
          { name: '가이드.doc', size: '123.12' },
          { name: '가이드.doc', size: '123.12' },
          { name: '가이드.doc', size: '123.12' },
        ]
      },
      {
        category: '반입신청',
        title: '반입신청',
        description: '반입신청 항목의 FAQ 제목 영역입니다. 관리자에서 등록합니다.',
        content: 'test입니다test입니다test입니다test입니다',
        isOpen: false,
        files: []
      },
      {
        category: '조치 계획',
        title: '조치 계획',
        description: '조치 계획 항목의 FAQ 제목 영역입니다. 관리자에서 등록합니다.',
        content: 'test입니다test입니다test입니다test입니다',
        isOpen: false,
        files: []
      },
      {
        category: '기타',
        title: '기타',
        description: '기타 항목의 FAQ 제목 영역입니다. 관리자에서 등록합니다.',
        content: 'test입니다test입니다test입니다test입니다',
        isOpen: false,
        files: []
      }
    ]);

    const currentTab = ref('전체');

    const tabs = computed(() => [
      { name: '전체', label: '전체', count: faqItems.value.length },
      ...['오픈소스 다운로드', '반입신청', '조치 계획', '기타'].map(category => ({
        name: category,
        label: category,
        count: faqItems.value.filter(item => item.category === category).length
      }))
    ]);

    const filteredFaqItems = computed(() => {
      if (currentTab.value === '전체') {
        return faqItems.value;
      }
      return faqItems.value.filter(item => item.category === currentTab.value);
    });

    const toggleContent = index => {
      filteredFaqItems.value[index].isOpen = !filteredFaqItems.value[index].isOpen;
    };

    return {
      faqItems,
      currentTab,
      tabs,
      filteredFaqItems,
      toggleContent
    };
  }
};
</script>
