<template>
  <div :class="['lnbMenu', activeClass]">
    <h2 class="lnbTit">{{ title }}</h2>
    <ul class="submenuList">
      <li
        v-for="menu in menus"
        :key="menu.title"
        :class="{ active: isMenuActive(menu), on: hasSubmenus(menu) }"
        class="submenu"
      >
        <a href="#" @click.prevent="handleClick(menu)">
          <span>{{ menu.title }}</span>
        </a>
        <ul v-if="hasSubmenus(menu) && isMenuWithSubmenuActive(menu)" class="depthMenuList">
          <li v-for="sub in menu.submenus" :key="sub.title" :class="{ active: isSubActive(sub.route) }">
            <router-link :to="sub.route" @click="handleSubClick(sub)">
              <span>{{ sub.title }}</span>
            </router-link>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      required: true
    },
    menus: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      activeMenu: null,
      activeSubMenu: null
    };
  },
  computed: {
    activeClass() {
      // 현재 경로에 따라 클래스 결정
      const path = this.$route.path;
      if (path.startsWith('/mysystem')) {
        return 'mysystem';
      } else if (path.startsWith('/community')) {
        return 'community';
      } else if (path.startsWith('/nhopensource')) {
        return 'nhopensource';
      } else if (path.startsWith('/system')) {
        return 'system';
      } else {
        return '';
      }
    }
  },
  methods: {
    handleClick(menu) {
      if (this.hasSubmenus(menu)) {
        this.toggleMenu(menu.title);
      } else {
        this.activeMenu = menu.title;
        this.activeSubMenu = null;
        if (menu.route) {
          this.$router.push(menu.route).catch(err => {
            console.error('Routing error:', err);
          });
        }
        this.$emit('update-title', menu.title);
      }
    },
    handleSubClick(sub) {
      this.activeSubMenu = sub.title;
      this.activeMenu = this.menus.find(menu => menu.submenus && menu.submenus.some(s => s.title === sub.title)).title;
      if (sub.route) {
        this.$router.push(sub.route).catch(err => {
          console.error('Routing error:', err);
        });
      }
      this.$emit('update-title', sub.title);
    },
    toggleMenu(title) {
      this.activeMenu = this.activeMenu === title ? null : title;
      if (this.activeMenu) {
        this.activeSubMenu = null;
      }
      this.$emit('update-title', null);
    },
    isActive(route) {
      return this.$route.path.startsWith(route);
    },
    isMenuActive(menu) {
      return this.activeMenu === menu.title || (this.hasSubmenus(menu) && menu.submenus.some(sub => this.isActive(sub.route)));
    },
    isSubActive(route) {
      return this.isActive(route);
    },
    hasSubmenus(menu) {
      return menu.submenus && menu.submenus.length > 0;
    },
    isMenuWithSubmenuActive(menu) {
      return this.hasSubmenus(menu) && (this.activeMenu === menu.title || menu.submenus.some(sub => this.isActive(sub.route)));
    },
    updateActiveMenu() {
      const path = this.$route.path;
      const activeMenuObj = this.menus.find(menu => 
        path.startsWith(menu.route) || 
        (this.hasSubmenus(menu) && menu.submenus.some(sub => this.isActive(sub.route)))
      );

      if (activeMenuObj) {
        this.activeMenu = activeMenuObj.title;
        const activeSubMenuObj = activeMenuObj.submenus?.find(sub => this.isActive(sub.route));
        this.activeSubMenu = activeSubMenuObj ? activeSubMenuObj.title : null;
      } else {
        this.activeMenu = null;
        this.activeSubMenu = null;
      }

      this.updateBreadcrumbs();
    },
    updateBreadcrumbs() {
      const breadcrumbs = [{ path: '/', title: '홈' }];

      if (this.activeMenu) {
        const activeMenuObj = this.menus.find(menu => menu.title === this.activeMenu);
        if (activeMenuObj) {
          breadcrumbs.push({
            path: activeMenuObj.route || null,
            title: activeMenuObj.title
          });

          if (this.activeSubMenu) {
            const activeSubMenuObj = activeMenuObj.submenus.find(sub => sub.title === this.activeSubMenu);
            if (activeSubMenuObj) {
              breadcrumbs.push({
                path: activeSubMenuObj.route,
                title: activeSubMenuObj.title
              });
            }
          }
        }
      }

      this.$emit('update-breadcrumbs', breadcrumbs);
    }
  },
  watch: {
    $route(to, from) {
      if (from.path !== to.path) {
        this.updateActiveMenu();
      }
    }
  },
  mounted() {
    this.updateActiveMenu();
  }
}
</script>
