<template>
    <div>
        <div class="tabs">
            <div
            v-for="tab in tabs"
            :key="tab.name"
            :class="['tab', {'active': tab.name === selectedTab}]"
            @click="selectedTab = tab.name"
            >   
                {{ tab.label }}
            </div>
        </div>
        <div class="tab-content">
            <slot :name="selectedTab"></slot>
        </div>
    </div>
</template>
<script>
export default {
    name: 'TabMenu',
    props: {
        tabs: {
            type: Array,
            required: true,
            default: () => []
        },
        applicationItem: {
            type: Array,
            required: true,
        }
    },
    data() {
        return {
            selectedTab: this.tabs.length > 0 ? this.tabs[0].name : ''
        };
    },
    watch: {
        tabs: {
            handler(newTabs) {
                if (!newTabs.some(tab => tab.name === this.selectedTab)) {
                    this.selectedTab = newTabs.length > 0 ? newTabs[0].name : '';
                }
            },
            deep : true
        }
    }
}
</script>
