                            <td>
                                <div class="radioStyle3"><label for="progress1"><input type="radio" id="progress1" class="radioStyle3" name="progress"><span class="on"></span></label></div>
                            </td>
                            <td>2</td>
                            <td>사용자1</td>
                            <td class="left">임직원 5,6급</td>
                        </tr>
                        <tr>
                            <td>
                                <div class="radioStyle3"><label for="progress1"><input type="radio" id="progress1" class="radioStyle3" name="progress"><span class="on"></span></label></div>
                            </td>
                            <td>3</td>
                            <td>사용자1</td>
                            <td class="left">임직원 5,6급</td>
                        </tr>
                        <tr>
                            <td>
                                <div class="radioStyle3"><label for="progress1"><input type="radio" id="progress1" class="radioStyle3" name="progress"><span class="on"></span></label></div>
                            </td>
                            <td>4</td>
                            <td>사용자1</td>
                            <td class="left">임직원 5,6급</td>
                        </tr>
                        <tr>
                            <td>
                                <div class="radioStyle3"><label for="progress1"><input type="radio" id="progress1" class="radioStyle3" name="progress"><span class="on"></span></label></div>
                            </td>
