                </table>
            </div>
        </div>
        <div>
            <div class="titW">
                <h3 class="h3tit">그룹별 메뉴 접근 정보</h3>
                <p class="titP">* 권한 그룹 목록에서 선택된 그룹의 메뉴 접근 정보를 저장/수정하실 수 있습니다</p>
            </div>
            <div class="scrollTable mb30">
                <table class="base-table">
