                        <col width="220px">
                        <col>
                    </colgroup>
                    <thead>
                        <tr>
                            <th>
                                <div class="tableCheckW">
                                    <div class="checkBoxBasic">
                                        <label for="checkAll" class="checkAll">
                                            <input type="checkbox" id="checkAll" v-model="selectAll" @change="toggleSelectAll">
                                            <span class="on"></span>
                                        </label>
                                    </div>
                                </div>
                            </th>
                            <th>
                                <div class="titleW">
                                    <span class="title">번호</span>
                                </div>
                            </th>
                            <th>
                                <div class="titleW">
                                    <span class="title">메뉴 그룹명</span>
                                    <button type="button" class="orderBtn"><img :src="orderArrow" alt="순서 화살표"></button>
                                </div>
                            </th>
                            <th>
                                <div class="titleW">
                                    <span class="title">메뉴 ID</span>
                                    <button type="button" class="orderBtn"><img :src="orderArrow" alt="순서 화살표"></button>
                                </div>
                            </th>
                            <th>
                                <div class="titleW">
                                    <span class="title">메뉴명</span>
                                    <button type="button" class="orderBtn"><img :src="orderArrow" alt="순서 화살표"></button>
                                </div>
                            </th>
                            <th>
                                <div class="titleW">
                                    <span class="title">메뉴 경로</span>
                                    <button type="button" class="orderBtn"><img :src="orderArrow" alt="순서 화살표"></button>
                                </div>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr class="tablePagenumBox" :class="{ hidden: !showSelectionOptions }">
                            <td colspan="6">
                                <div class="txflex">
                                    <p class="pagetx">현재 페이지에서 총 <span class="num">{{ nonDisabledItems.length }}</span>개를 선택하였습니다.</p>
                                    <p class="listtx allselect" v-if="!allItemsSelected" @click="selectAllItems">전체 목록에서 총 <span class="num">{{ nonDisabledItems.length }}</span>건 일괄 선택하기</p>
                                    <p class="listtx allcancel" v-if="allItemsSelected" @click="deselectAllItems">전체 선택 취소</p>
                                </div>
                            </td>
                        </tr>
                        <tr v-for="item in items" :key="item.id">
                            <td class="checktd">
                                <div :class="['checkBoxBasic', { disable: item.disable }]">
                                    <label :for="'check' + item.id">
                                        <input type="checkbox" :id="'check' + item.id" v-model="checkedItems" :value="item.id">
                                        <span class="on"></span>
                                    </label>
                                </div>
                            </td>
                            <td>
                                <span>{{ item.menuNum }}</span>
                            </td>
                            <td>
                                <span>{{ item.menugroup }}</span>
                            </td>
                            <td>
                                <span>{{ item.menuId }}</span>
                            </td>
                            <td>
                                <span>
                                    {{ item.menuName }}
                                </span>
                            </td>
                            <td>
                                <span>{{ item.menuRoute }}</span>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="relative clearfix">
                <Pagination class="aliginCenter"/>
                <router-link to="/" class="BtnStyle1 floatR">
                    <span>저장</span>
                </router-link>
            </div>
        </div>
    </div>
        <BasicPopup
        v-if="showPopup"
        :title="popupTitle"
        :footerText="popupFooterText"
        :footerSubText="popupFooterSubText" 
        :footerLink="popupFooterLink"
        :footerSubLink="popupFooterSubLink" 
        @close="showPopup = false"
        >
            <component :is="popupContent"></component>
        </BasicPopup>
</template>
<script>
import Pagination from '@/components/ui/Pagination.vue'
import BasicPopup from '@/layouts/popup/BasicpopUp.vue'
import UserinformationpopUp from '@/components/ui/popup/UserinformationpopUp.vue'
import AuthGroupWritepopUp from '@/components/ui/popup/AuthGroupWritepopUp.vue'
import { ref } from 'vue';

export default {
  name: 'UserBusnSys',
  components: {
    Pagination,
    BasicPopup,
    AuthGroupWritepopUp,
    UserinformationpopUp
  },
  data() {
    return {
      items: [
        { id: 1, menuNum: '1',  menugroup: 'My 시스템', menuId: 'NHUHMY-01', menuName: '대시보드', menuRoute: 'My시스템>대시보드'},
        { id: 2, menuNum: '2',  menugroup: 'My 시스템', menuId: 'NHUHMY-01', menuName: '대시보드', menuRoute: 'My시스템>대시보드'},
        { id: 3, menuNum: '3',  menugroup: 'My 시스템', menuId: 'NHUHMY-01', menuName: '대시보드', menuRoute: 'My시스템>대시보드'},
        { id: 4, menuNum: '4',  menugroup: 'My 시스템', menuId: 'NHUHMY-01', menuName: '대시보드', menuRoute: 'My시스템>대시보드'},
        { id: 5, menuNum: '5',  menugroup: 'My 시스템', menuId: 'NHUHMY-01', menuName: '대시보드', menuRoute: 'My시스템>대시보드'},
        { id: 6, menuNum: '6',  menugroup: 'My 시스템', menuId: 'NHUHMY-01', menuName: '대시보드', menuRoute: 'My시스템>대시보드'},
        { id: 7, menuNum: '7',  menugroup: 'My 시스템', menuId: 'NHUHMY-01', menuName: '대시보드', menuRoute: 'My시스템>대시보드'},
        { id: 8, menuNum: '8',  menugroup: 'My 시스템', menuId: 'NHUHMY-01', menuName: '대시보드', menuRoute: 'My시스템>대시보드'},

      ],
      checkedItems: [],
      selectAll: false,
      showSelectionOptions: false,
      allItemsSelected: false,
      orderArrow: require('@/assets/images/sub/orderArrow.png'), 
    };
  },
  computed: {
    nonDisabledItems() {
      return this.items.filter(item => !item.disable);
    }
  },
  methods: {
    toggleSelectAll(event) {
      this.selectAll = event.target.checked;
      if (this.selectAll) {
        this.checkedItems = this.nonDisabledItems.map(item => item.id);
        this.allItemsSelected = true;
      } else {
        this.checkedItems = [];
        this.allItemsSelected = false;
      }
      this.showSelectionOptions = this.checkedItems.length > 0;
    },
    selectAllItems() {
      this.checkedItems = this.nonDisabledItems.map(item => item.id);
      this.allItemsSelected = true;
      this.showSelectionOptions = true;
    },
    deselectAllItems() {
      this.checkedItems = [];
      this.allItemsSelected = false;
      this.showSelectionOptions = false;
    },
  },
  watch: {
    checkedItems(newVal) {
      const selectableItemsCount = this.nonDisabledItems.length;
      this.selectAll = newVal.length === selectableItemsCount;
      this.showSelectionOptions = newVal.length > 0;
      this.allItemsSelected = this.selectAll;
    }
  },
  setup() {
  const showPopup = ref(false);
  const popupContent = ref(null);
  const popupTitle = ref('');
  const popupFooterText = ref('확인');
  const popupFooterSubText = ref('');
  const popupFooterLink = ref('/');
  const popupFooterSubLink = ref(null);  // 두 번째 버튼이 없으면 null

  const openUserinformationpopUp = () => {
    popupContent.value = UserinformationpopUp;
    popupTitle.value = "사용자 정보";
    showPopup.value = true;
    popupFooterText.value = "저장";
    popupFooterLink.value = "/"; 
    popupFooterSubLink.value = null; 
    popupFooterSubText.value = ''; 
  };

  const openAuthGroupWritepopUp = () => {
    popupContent.value = AuthGroupWritepopUp;
    popupTitle.value = "신규 그룹 생성 팝업";
    showPopup.value = true;
    popupFooterText.value = "등록";
    popupFooterSubText.value = "취소";
    popupFooterLink.value = "/"; 
    popupFooterSubLink.value = "/"; 
  };


    return {
      showPopup,
      popupContent,
      popupTitle,
      popupFooterLink,
      popupFooterSubLink,
      popupFooterText,
      popupFooterSubText,
      openUserinformationpopUp,
      openAuthGroupWritepopUp,
    };
  },
  

};
</script>

