<template>
  <div class="CommentCon">
    <ul class="comentList">
      <li v-for="(comment, index) in comments" :key="index" :class="{ user: comment.identity === '사용자' }">
        <div class="identityBox">
          <span class="identity">{{ comment.identity || '사용자' }}</span>
        </div>
        <div class="comentW">
          <ul>
            <li class="team">{{ comment.team }}</li>
            
            <li class="name">{{ comment.name }}</li>
            <li class="creationTime">{{ comment.creationTime }}</li>
          </ul>
          <p>
            {{ comment.text }}
            <span class="comentClose" v-if="comment.identity === '사용자'" @click="deleteComment(index)">삭제</span>
          </p>
        </div>
      </li>
    </ul>
    <div class="comentBox">
      <div class="comentTop">
        <ul class="comenttitList">
          <li class="team">IT품질관리팀</li>
          <li class="name">김농협</li>
          <li class="hour">2024-12-30 09:16</li>
        </ul>
      </div>
      <div class="comentForm">
        <form @submit.prevent="submitComment">
          <div class="comentBox">
            <textarea
              v-model="newComment"
              placeholder="댓글을 남겨보세요 최대 200글자 입력 가능합니다."
              class="comenttxarea"
            ></textarea>
            <div class="writeNum">
              <span class="current">{{ newComment.length }}</span> / <span class="total">200</span>
            </div>
          </div>
          <button type="submit" class="comentBtn">등록</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommentSection',
  props: {
    comments: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      newComment: ''
    };
  },
  methods: {
    submitComment() {
      if (this.newComment.trim().length === 0 || this.newComment.length > 200) {
        return; // 유효성 검사
      }
      const commentData = {
        identity: '사용자', // 댓글 작성자의 identity, 필요에 따라 수정
        team: 'IT품질관리팀',
        name: '홍길동', // 이름 필요에 따라 수정
        creationTime: new Date().toLocaleString(),
        text: this.newComment
      };
      this.$emit('add-comment', commentData);
      this.newComment = ''; // 댓글 전송 후 텍스트 초기화
    },
    deleteComment(index) {
      this.$emit('delete-comment', index);
    }
  }
};
</script>
