      orderArrow: require('@/assets/images/sub/orderArrow.png'), 
      selectedCount: 0, 
    };
  },
  computed: {
    nonDisabledItems() {
      return this.items.filter(item => !item.disable);
    }
  },
  methods: {
    toggleSelectAll(event) {
      this.selectAll = event.target.checked;
      if (this.selectAll) {
        this.checkedItems = this.nonDisabledItems.map(item => item.id);
        this.allItemsSelected = true;
      } else {
        this.checkedItems = [];
        this.allItemsSelected = false;
      }
      this.showSelectionOptions = this.checkedItems.length > 0;
    },
    selectAllItems() {
      this.checkedItems = this.nonDisabledItems.map(item => item.id);
      this.allItemsSelected = true;
      this.showSelectionOptions = true;
    },
    deselectAllItems() {
      this.checkedItems = [];
      this.allItemsSelected = false;
      this.showSelectionOptions = false;
    },
    riskClass(risk) {
      switch (risk) {
        case '긴급': return 'emergency';
        case '높음': return 'high';
        case '중간': return 'mid';
        case '낮음': return 'low';
        default: return '';
      }
    }
  },
  watch: {
    checkedItems(newVal) {
      const selectableItemsCount = this.nonDisabledItems.length;
      this.selectAll = newVal.length === selectableItemsCount;
      this.showSelectionOptions = newVal.length > 0;
      this.allItemsSelected = this.selectAll;
      this.selectedCount = newVal.length;  
    }
  },
  
  setup() {
    const currentTab = ref('library');
    const showPopup = ref(false);
    const popupContent = ref(null);
    const popupTitle = ref('');
    const popupFooterText = ref('확인');
    const popupFooterLink = ref('/'); 

    const tabs = computed(() => [
      { name: 'library', label: '라이브러리' },
      { name: 'software', label: '소프트웨어' },
      { name: 'solution', label: '솔루션' },
    ]);

    const openCvsspopUp = () => {
      popupContent.value = CvsspopUp;
      popupTitle.value = "CVSS(공통 취약점 등급 시스템)란?";
      showPopup.value = true;
    };

    const openActplanRegipopUp = () => {
      popupContent.value = ActplanRegipopUp;
      popupTitle.value = "조치 계획 등록/상신";
      showPopup.value = true;
      popupFooterText.value = "결재 상신";
    };

    const openExceptionpopUp = () => {
      popupContent.value = ExceptionListpopUp;
      popupTitle.value = "예외처리 이력";
      showPopup.value = true;
      popupFooterText.value = "확인";
    };

    const openNoActplanRegipopopUp = () => {
      popupContent.value = NoActplanRegipopopUp;
      popupTitle.value = "조치계획 등록 불가 안내";
      showPopup.value = true;
      popupFooterText.value = "확인";
    };

    return {
      openNoActplanRegipopopUp,
      openExceptionpopUp,
      openCvsspopUp,
      openActplanRegipopUp,
      showPopup,
      popupContent,
      popupTitle,
      currentTab,
      tabs,
      popupFooterLink,
      popupFooterText
    };
  },
};
