<template>
  <div class="BasicpopUp">
    <div class="popupBox">
      <div class="popupHeader">
        <span class="title">{{ title }}</span>
        <button type="button" class="popudpClose" @click="$emit('close')">
          <img src="@/assets/images/sub/popupClose.png" alt="팝업 닫기버튼">
        </button>
      </div>
      <div class="popupContent">
        <slot></slot>
      </div>
      <div class="popupFooter">

        <!-- 두 번째 버튼이 있을 때만 두 개의 버튼을 보여줌 -->
        <div class="btnW flex-end">
          <router-link :to="footerLink" class="BtnStyle1">
            <span>{{ footerText }}</span>
          </router-link>
          <!-- footerSubLink가 있을 때만 두 번째 버튼을 렌더링 -->
          <router-link v-if="footerSubLink" :to="footerSubLink" class="BtnStyle">
            <span>{{ footerSubText }}</span>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BasicPopup',
  props: {
    title: {
      type: String,
      default: '팝업 제목'
    },
    footerText: {
      type: String,
      default: '확인'
    },
    footerSubText: {
      type: String,
      default: ''
    },
    footerLink: {
      type: String,
      default: '/'
    },
    footerSubLink: {
      type: String,
      default: '' 
    },
    showCheckBox: {
      type: Boolean,
      default: false
    }
  }
}
</script>
