<template>
  <header 
    :class="{'header': true, 'on': isMenuActive}" 
    @mouseleave="handleMouseLeave"
  >
    <div class="inner">
      <h1><router-link to="/"><img src="@/assets/images/common/mainLogo.png" alt="로고이미지입니다."></router-link></h1>
      <div class="gnbMenu">
        <ul class="menuList">
          <li
            v-for="menu in headerMenus"
            :key="menu.title"
            :class="{ active: isActiveMenu(menu) }"
            @mouseover="handleMouseOver(menu)"
          >
            <button type="button" @click="handleMenuClick(menu)">
              {{ menu.title }}
            </button>
            <div v-if="menu.submenus" class="subMenu">
              <ul class="depth2List">
                <li v-for="submenu in menu.submenus" :key="submenu.title">
                  <router-link :to="submenu.route" @click="handleLinkClick">{{ submenu.title }}</router-link>
                  <ul v-if="submenu.submenus && submenu.submenus.length > 0" class="depth3List">
                    <li v-for="subsubmenu in submenu.submenus" :key="subsubmenu.title">
                      <router-link :to="subsubmenu.route" @click="handleLinkClick">{{ subsubmenu.title }}</router-link>
                    </li>
                  </ul>
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
      <div class="loginW">
        <div class="login">
          <span class="userName">김농협님(1234567)</span>
          <a href="#" class="logBtn">로그아웃</a>
        </div>
      </div>
    </div>
    <div class="headerBg">
      <div class="bgInner">
        <div>
          <p><span>오픈소스 관리포털</span>에<br>
            오신걸 환영합니다.</p>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      headerMenus: [],
      isMenuActive: false,
      activeMenu: null 
    };
  },
  created() {
    this.generateMenus();
    this.setActiveMenuFromRoute();
  },
  watch: {
    $route() {
      this.setActiveMenuFromRoute();
    }
  },
  methods: {
    generateMenus() {
      const routes = this.$router.options.routes;
      const menuData = [];

      routes.forEach(route => {
        if (route.meta && route.meta.sidebarTitle && route.meta.menus) {
          const submenus = route.meta.menus.map(menu => ({
            title: menu.title,
            route: menu.route,
            submenus: menu.submenus || []
          }));

          menuData.push({
            title: route.meta.sidebarTitle,
            submenus
          });
        }
      });

      this.headerMenus = menuData;
    },
    setActiveMenuFromRoute() {
      const currentPath = this.$route.path;

      this.activeMenu = this.headerMenus.find(menu =>
        menu.submenus.some(submenu => {
          if (submenu.route === currentPath) {
            return true;
          }

          return submenu.submenus && submenu.submenus.some(subsubmenu => subsubmenu.route === currentPath);
        })
      );
    },
    handleMouseOver(menu) {
      this.isMenuActive = true;
      this.activeMenu = menu;
    },
    handleMouseLeave() {
      this.isMenuActive = false;
      this.activeMenu = null;
    },
    handleMenuClick(menu) {
      if (menu.submenus && menu.submenus.length > 0) {
        const firstSubmenu = menu.submenus[0];
        this.$router.push(firstSubmenu.route);
      } else if (menu.route) {
        this.$router.push(menu.route);
      }
      this.resetMenuState();
    },
    handleLinkClick() {
      this.resetMenuState();
    },
    resetMenuState() {
      this.isMenuActive = false;
      this.activeMenu = null;
    },
    isActiveMenu(menu) {
      const currentPath = this.$route.path;
      if (menu.route === currentPath) {
        return true;
      }

      if (menu.submenus && menu.submenus.some(submenu => submenu.route === currentPath)) {
        return true;
      }


      if (menu.route && currentPath.startsWith(menu.route)) {
        return true;
      }

      return menu.submenus && menu.submenus.some(submenu => currentPath.startsWith(submenu.route));
    }
  }
}
</script>
