<template>
  <div class="Atachment RegistrationCon">
    
    <div class="row" v-if="showManagementNumber">
      <div class="thead">
        <span class="title">관리 번호<span>*</span></span>
      </div>
      <div class="tbody">
        <div class="btnW">
          <input type="text" class="inputStyle2 inputTextWd1" placeholder="관리번호를 입력하세요.">
          <p class="textStyle1">* 시스템관리 > NH오픈소스 관리 > 반입/사용 신청 관리 > 라이브러리 유형 신청 번호</p>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="thead"><span class="filetit">첨부파일</span></div>
      <div class="tbody">
        <div class="fileBtnW">
          <label class="fileBtn">
            <input type="file" @change="handleFileUpload" multiple class="fileInput" />
            파일첨부
          </label>
          <span class="format">* 첨부가능 포맷 : dat / * 파일 1개당 최대 30 mb등록 가능</span>
        </div>
       
        <div class="fileW" v-if="files.length > 0">
          <ul>
            <li v-for="(file, index) in files" :key="index">
              <a href="#">
                <span class="title">{{ file.name }}</span>
                <span class="kb">({{ (file.size / 1024).toFixed(2) }}kb)</span>
                <span class="fileClose" @click="removeFile(index)">삭제</span>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="row" v-if="showMainExposure">
      <div class="thead"><span class="detail">메인 노출 여부</span></div>
      <div class="tbody">
        <div class="checkListBox">
          <div class="category_radioBox">
              <div class="radioW">
                  <div class="radioStyle"><label for="progress2_1"><input type="radio" id="progress2_1" class="radioStyle" name="progress2" checked=""><span class="on"></span> 노출 </label></div>
                  <div class="selectForm"><select name="" id="" class="wd80"><optgroup><option value="" selected="">1</option><option value="">30</option><option value="">50</option><option value="">100</option></optgroup></select></div>
                  <div class="radioStyle"><label for="progress2_2"><input type="radio" id="progress2_2" class="radioStyle" name="progress2"><span class="on"></span> 비노출 </label></div>
              </div>
          </div>
          <p>* 노출 선택 시, 메인 노출 순서(1,2,3)를 입력해주세요.</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Atachment',
  props: {
    showMainExposure: {
      type: Boolean,
      default: false
    },
    showManagementNumber: {
      type: Boolean,
      default: true 
    }
  },
  data() {
    return {
      files: [] 
    };
  },
  methods: {
    handleFileUpload(event) {
      const uploadedFiles = Array.from(event.target.files);
      uploadedFiles.forEach(file => {
        if (this.files.length < 10) { 
          this.files.push(file);
        }
      });
      event.target.value = ''; 
    },
    removeFile(index) {
      this.files.splice(index, 1); 
    }
  }
};
</script>


