<template>
    <div class="opensourceSection">
        <div class="opentitBox">
            <h2 class="h2tit">오픈소스 다운로드 TOP 5</h2>
            <span class="date">7월 1일 기준</span>
        </div>
        <div class="opensourceW">
            <ul class="opensourceList">
                <li v-for="item in opensourceData" :key="item.id">
                    <div class="topArea">
                        <span class="Number">{{item.Number}}</span>
                        <span :class="getClass(item.category)">{{item.category}}</span>
                    </div>
                    <div class="middleArea">
                        <h3 class="productName">{{item.title}}</h3>
                        <p class="productTx"><a href="#">{{item.content}}</a></p>
                    </div>
                    <div class="btmArea">
                        <div class="versionW">
                            <span class="versionN">버전</span>
                            <span class="versionNum">{{item.versionNum}}</span>
                        </div>
                        <div class="downW">
                            <a href="#"><img src="@/assets/images/main/downIcon.png" alt="다운로드 아이콘"></a>
                            <span class="downN">{{item.downloadNum}}</span>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name: 'OpensourceSection',

    data() {
        return {
            opensourceData: [
                {id: 1, Number: '01', category: '설치형', title: '제품명1', content: "opensourceData text입니다.", versionNum: 8557, downloadNum: 7352},
                {id: 2, Number: '02', category: '라이브러리', title: '제품명2', content: "opensourceData text입니다.", versionNum: 8557, downloadNum: 7352},
                {id: 3, Number: '03', category: '설치형', title: '제품명3', content: "opensourceData text입니다.", versionNum: 8557, downloadNum: 7352},
                {id: 4, Number: '04', category: '라이브러리', title: '제품명4', content: "opensourceData text입니다.", versionNum: 8557, downloadNum: 7352},
                {id: 5, Number: '05', category: '설치형', title: '제품명5', content: "opensourceData text입니다.", versionNum: 8557, downloadNum: 7352},
            ]
        }
    },
    methods: {
        getClass(text) {
            const baseClass = 'category'; 

            switch (text) {
                case '설치형' : 
                    return [baseClass, 'install']; 
                case '라이브러리' : 
                    return [baseClass, 'library']; 
                default : 
                    return [baseClass]; 
            }
        }
    }
}
</script>
