<template>
    <div :class="['basicTultip', customWidthClass]">
        <ul>
            <li v-for="(text, index) in texts" :key="index">
                <span v-html="text"></span>
            </li>
        </ul>
        <button type="button" class="tultipClose" @click="$emit('close-tooltip')">
            <img src="@/assets/images/sub/tultupClose.png" alt="툴팁닫기아이콘">
        </button>
    </div>
</template>

<script>
export default {
    name: 'BasicTultip',
    props: {
        texts: {
            type: Array,
            required: true,
            default: () => []
        },
        customWidthClass: {
            type: String,
            required: false,
            default: ''
        }
    }
}
</script>


