<template>
  <div class="taskListCon">
    <div>
      <div class="pagenavigation" v-if="taskitems.length > itemsPerPage">
        <button
          v-for="(page, index) in pageCount"
          :key="index"
          :class="{ active: currentPage === index }"
          @click="goToPage(index)"
          type="button"
        ></button>
      </div>
      <div class="taskList">
        <button
          type="button"
          class="leftArrow teskArrow"
          v-if="currentPage > 0"
          @click="prevPage"
        >
          <img src="@/assets/images/sub/teskArrow.png" alt="왼쪽 화살표" />
        </button>
        <div class="taskCon">
          <div class="taskW">
            <ul :style="taskListStyle">
              <li v-for="(item, index) in taskitems" :key="index">
                <div class="logoImg">
                  <img src="@/assets/images/sub/nonghyupIcon.png" alt="농협 아이콘" />
                </div>
                <div class="textW">
                  <h3>{{ item.title }}</h3>
                  <p>{{ item.description }}</p>
                </div>
              </li>
              <li class="worksystem">
                <div>업무 시스템</div>
              </li>
            </ul>
          </div>
        </div>
        <button
          type="button"
          class="rightArrow teskArrow"
          v-if="currentPage < pageCount - 1"
          @click="nextPage"
        >
          <img src="@/assets/images/sub/teskArrow.png" alt="오른쪽 화살표" />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TaskSlider',
  props: {
    taskitems: {
      type: Array,
      required: true
    },
    itemsPerPage: {
      type: Number,
      default: 5
    }
  },
  data() {
    return {
      currentPage: 0,
    };
  },
  computed: {
    pageCount() {
      return Math.ceil(this.taskitems.length / this.itemsPerPage);
    },
    taskListStyle() {
      return {
        transform: `translateX(-${this.currentPage * 100}%)`,
        transition: 'transform 0.3s ease',
      };
    },
  },
  methods: {
    nextPage() {
      if (this.currentPage < this.pageCount - 1) {
        this.currentPage += 1;
      }
    },
    prevPage() {
      if (this.currentPage > 0) {
        this.currentPage -= 1;
      }
    },
    goToPage(index) {
      this.currentPage = index;
    },
  },
};
</script>

