<template>
  <div class="btnW">
    <button
      v-for="(label, index) in labels"
      :key="index"
      type="button"
      class="periodBtn"
      :class="{ on: selectedIndex === index }"
      @click="selectButton(index)"
    >
      {{ label }}
    </button>
  </div>
</template>

<script>
import { ref } from 'vue';

export default {
  name: "PeriodButtonGroup",
  props: {
    labels: {
      type: Array,
      default: () => ["1주일", "1개월", "3개월"]
    }
  },
  setup(props, { emit }) {
    const selectedIndex = ref(null);

    const selectButton = (index) => {
      selectedIndex.value = index;
      emit('button-click', props.labels[index]);
    };

    return {
      selectedIndex,
      selectButton
    };
  }
};
</script>
