            popupTitle.value = "취약점 정보 조회";
            showPopup.value = true;
            popupFooterText.value = "저장";
            popupFooterLink.value = "/";
        };


        return {
            showPopup,
            popupContent,
            popupTitle,
            popupFooterText,
            popupFooterLink,
            openChecklistpopUp,
            openOsspopUp,
            openWeaknesspopUp
