<template>
  <div class="notificationCon">
    <div class="topNotice">
      <div class="titleW">
        <span :class="['category', {
          important: category === '중요',
          complete: category === '답변완료',
          atmosphere: category === '답변대기'
        }]">{{ category }}</span>
        <span class="title">{{ title }} <span class="num">({{ fileCount }})</span></span>
      </div>
      <div class="titW" v-if="belong || name || date">
        <span class="belong" v-if="belong">{{ belong }}</span>
        <span class="name" v-if="name">{{ name }}</span>
        <span class="date" v-if="date">{{ date }}</span>
      </div>
    </div>
    <div class="noticeScript">
      <p>{{ content }}</p>
    </div>
    <div class="fileW">
      <ul>
        <li v-for="(file, index) in files" :key="index">
          <a :href="file.url">
            <span class="title">{{ file.name }}</span>
            <span class="kb">({{ file.size }})</span>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NotificationDetail',
  props: {
    category: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    date: {
      type: String,
      required: true
    },
    imgSrc: {
      type: String,
      required: false
    },
    content: {
      type: String,
      required: true
    },
    files: {
      type: Array,
      required: true
    },
    belong: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    }
  },
  computed: {
    fileCount() {
      return this.files.length;
    }
  }
};
</script>
