<template>
    <table class="base-table">
      <colgroup>
        <col v-for="(width, index) in columnWidths" :key="index" :style="{ width: width }">
      </colgroup>
      <thead>
        <tr>
          <th v-for="(header, index) in headers" :key="index">{{ header }}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(row, rowIndex) in rows" :key="rowIndex">
          <td v-for="(cell, cellIndex) in row" :key="cellIndex" 
              :class="[{ important: cell.isImportant }, { left: cell.isLink }, cell.className]">
            <span v-if="cell.isImportant">
              <span class="important">{{ cell.content }}</span>
            </span>
            <p v-else-if="cell.isLink" class="tablecontentP">
              <span v-if="cell.status === '답변대기'" class="status status-waiting">답변대기</span>
              <span v-if="cell.status === '답변완료'" class="status status-completed">답변완료</span>
              <router-link :to="cell.to">{{ cell.content }}</router-link>
              <span v-if="cell.hasAttachment" class="clip-icon"></span>
              <span v-if="cell.commentCount" class="comment-count">({{ cell.commentCount }})</span>
            </p>
            <span v-else-if="typeof cell === 'object' && cell.content">
              {{ cell.content }}
              <span v-if="cell.additionalInfo">{{ cell.additionalInfo }}</span>
            </span>
            <span v-else :class="cell === '긴급' ? 'risk emergency' : ''">
              {{ cell }}
            </span>
          </td>
        </tr>
      </tbody>
    </table>
</template>


  
  <script>
  export default {
    props: {
      headers: {
        type: Array,
        required: true
      },
      rows: {
        type: Array,
        required: true
      },
      columnWidths: {
        type: Array,
        default: () => []
      }
    }
  };
  </script>
  
