            popupContent.value = IssueHistory;
            popupTitle.value = "긴급 이슈 발생 이력 보기";
            showPopup.value = true;
        };

        return {
            showPopup,
            popupContent,
            popupTitle,
            openVulnerabilityPopup,
            openLicensepopUp,
            openIssueHistory
        };
    }
}
