<template>
  <div class="category_radioBox" @click="handleOutsideClick">
    <div class="inputdateBox">
      <input 
        class="inputDate"
        type="text" 
        id="startDate" 
        v-model="formattedStartDate" 
        @focus="showStartCalendar = true" 
        readonly
      />
      <DatePicker
        v-if="showStartCalendar"
        v-model="selectedStartDate"
        @close="closeStartCalendar"
        @update:model-value="closeStartCalendar"
        :attributes="startRangeAttributes"
        :popover="{ visibility: 'focus' }"
      />
    </div>
    <span>-</span>
    <div class="inputdateBox">
      <input 
        class="inputDate"
        type="text" 
        id="endDate" 
        v-model="formattedEndDate" 
        @focus="showEndCalendar = true" 
        readonly
      />
      <DatePicker
        v-if="showEndCalendar"
        v-model="selectedEndDate"
        @close="closeEndCalendar"
        @update:model-value="closeEndCalendar"
        :attributes="endRangeAttributes"
        :popover="{ visibility: 'focus' }"
      />
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onBeforeUnmount } from 'vue';
import { DatePicker } from 'v-calendar';

const selectedStartDate = ref(new Date(2024, 8, 15)); 
const selectedEndDate = ref(new Date(2024, 8, 19)); 

const showStartCalendar = ref(false);
const showEndCalendar = ref(false);

const startRangeAttributes = ref([
  {
    key: 'start-date',
    highlight: {
      backgroundColor: '#b3d4fc',
      borderColor: '#4d90fe',
    },
    dates: selectedStartDate
  }
]);

const endRangeAttributes = ref([
  {
    key: 'end-date',
    highlight: {
      backgroundColor: '#b3d4fc',
      borderColor: '#4d90fe',
    },
    dates: selectedEndDate
  }
]);

const formattedStartDate = computed(() => {
  return selectedStartDate.value ? selectedStartDate.value.toLocaleDateString() : '';
});

const formattedEndDate = computed(() => {
  return selectedEndDate.value ? selectedEndDate.value.toLocaleDateString() : '';
});

// 외부 클릭 감지 및 처리
const handleOutsideClick = (event) => {
  if (!event.target.closest('.inputdateBox')) {
    showStartCalendar.value = false;
    showEndCalendar.value = false;
  }
};

const closeStartCalendar = () => {
  showStartCalendar.value = false;
};

const closeEndCalendar = () => {
  showEndCalendar.value = false;
};

// Mounted와 Unmounted 라이프사이클 훅을 사용하여 이벤트 리스너 추가 및 제거
onMounted(() => {
  document.addEventListener('click', handleOutsideClick);
});

onBeforeUnmount(() => {
  document.removeEventListener('click', handleOutsideClick);
});

</script>
