<template>
  <div class="category_radioBox" @click="handleOutsideClick">
    <div class="inputBox">
      <input 
        class="inputDate basic"
        type="text" 
        id="singleDate" 
        v-model="formattedDate" 
        @focus="showCalendar = true" 
        readonly
      />
      <DatePicker
        v-if="showCalendar"
        v-model="selectedDate"
        @close="closeCalendar"
        @update:model-value="closeCalendar"
        :popover="{ visibility: 'focus' }"
      />
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onBeforeUnmount } from 'vue';
import { DatePicker } from 'v-calendar';

const selectedDate = ref(new Date(2024, 8, 15)); 
const showCalendar = ref(false);

const formattedDate = computed(() => {
  return selectedDate.value ? selectedDate.value.toLocaleDateString() : '';
});

const handleOutsideClick = (event) => {
  if (!event.target.closest('.inputBox')) {
    showCalendar.value = false;
  }
};

const closeCalendar = () => {
  showCalendar.value = false;
};

onMounted(() => {
  document.addEventListener('click', handleOutsideClick);
});

onBeforeUnmount(() => {
  document.removeEventListener('click', handleOutsideClick);
});

</script>

