<template>
    <div class="sub_Wrapper">
      <Header></Header>
      <div class="sub_contents">
        <div class="inner">
            <Error></Error>
        </div>
      </div>
      <Footer></Footer>
      <ErrorpopUp v-if="isErrorPopUpVisible" @close="isErrorPopUpVisible = false"></ErrorpopUp>
    <!-- 팝업 컴포넌트 -->
    <BasicPopup
      v-if="isPopUpVisible"
      :title="popupTitle"
      :footerText="popupFooterText"
      :footerLink="popupFooterLink"
      :showCheckBox="true" 
      @close="isPopUpVisible = false"
    >
      <component :is="popupContent"></component>
    </BasicPopup>
    </div>
  </template>
  
  
  <script>
  import Header from '@/components/Header.vue'
  import Footer from '@/components/Footer.vue'
  import Error from '@/components/ui/error/Error.vue';
  import ErrorpopUp from '@/components/ui/popup/ErrorpopUp.vue'
  import BasicPopup from '@/layouts/popup/BasicpopUp.vue'
  import MainpopUp from '@/components/ui/popup/MainpopUp.vue'


  export default {
    name: 'ErrorLayout',
    components: {
      Header,
      Footer,
      Error,
      ErrorpopUp,
      BasicPopup,
      MainpopUp
    },

    
    data() {
    return {
      isPopUpVisible: true,           // BasicPopup 표시 여부
      isErrorPopUpVisible: true,     // ErrorpopUp 표시 여부
      popupTitle: '공지사항',    // 기본 팝업 제목
      popupFooterText: '확인',        // 기본 하단 버튼 텍스트
      popupFooterLink: '/',           // 기본 하단 버튼 링크
      popupContent: 'MainpopUp',      // 기본으로 표시할 컴포넌트
      
    };
  },
  }
  </script>
  