<template>
  <div class="sub_Wrapper">
    <Header></Header>
    <div class="sub_contents">
      <div class="inner">
        <SidebarMenu :title="sidebarTitle" :menus="pageMenus" @update-title="updateLocationTitle" />
        <div class="sub_content">
          <div class="locationW">
            <h2 class="locationTit">{{ currentTitle }}</h2>
            <div class="location">
              <ul>
                <li v-for="(crumb, index) in breadcrumbs" :key="index">
                  <span v-if="!crumb.path">{{ crumb.title }}</span>
                  <router-link v-else :to="crumb.path">{{ crumb.title }}</router-link>
                </li>
              </ul>
            </div>
          </div>
          <div class="content">
            <router-view @route-changed="updateLocationTitle"></router-view> <!-- 자식 라우트는 여기에서 렌더링됩니다. -->
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import '@/assets/css/sub.css'
import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'
import SidebarMenu from '@/components/SidebarMenu.vue'

export default {
  name: 'SubLayout',
  components: {
    Header,
    Footer,
    SidebarMenu
  },
  data() {
    return {
      sidebarTitle: '',
      pageMenus: [],
      currentTitle: '',
      breadcrumbs: []
    };
  },
  watch: {
    $route(to) {
      this.setPageData(to);
    }
  },
  created() {
    this.setPageData(this.$route);
  },
  methods: {
    setPageData(route) {
      const { meta, matched } = route;
      this.sidebarTitle = meta.sidebarTitle || '';
      this.pageMenus = meta.menus || [];
      this.currentTitle = meta.title || '';
      this.updateBreadcrumbs(matched);
    },
    updateLocationTitle(title) {
      if (title) {
        this.currentTitle = title;
      } else {
        const route = this.$route;
        this.currentTitle = route.meta.title || '';
      }
    },
    updateBreadcrumbs(matchedRoutes) {
      const breadcrumbs = [{ path: '/', title: '홈' }];
      
      matchedRoutes.forEach(route => {
        if (route.meta && route.meta.sidebarTitle) {

          const parentMenu = route.meta.menus?.find(menu => menu.route === route.path);
          if (parentMenu) {
            breadcrumbs.push({
              path: null, 
              title: parentMenu.title,
            });

            parentMenu.submenus?.forEach(sub => {
              if (route.path === sub.route) {
                breadcrumbs.push({
                  path: parentMenu.route,  
                  title: parentMenu.title,
                });
                breadcrumbs.push({
                  path: sub.route, 
                  title: sub.title,
                });
              }
            });
          } else {
            breadcrumbs.push({
              path: route.path,
              title: route.meta.sidebarTitle,
            });
          }
        } else if (route.meta && route.meta.title && !breadcrumbs.some(crumb => crumb.title === route.meta.title)) {
          breadcrumbs.push({
            path: route.path,
            title: route.meta.title,
          });
        }
      });

      this.breadcrumbs = breadcrumbs;
    }
  }
}
</script>
