<template>
    <div class="FlexTable">
        <div class="row" v-for="(item, index) in tableData" :key="index">
            <div class="thead">
                <span class="title">{{ item.title }}</span>
            </div>
            <div class="tbody">
                <span v-if="!item.isList">{{ item.content }}</span>
                <ul v-else class="comentList">
                    <li v-for="(comment, idx) in item.content" :key="idx">{{ comment }}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'FlexTable',
    props: {
        tableData: {
            type: Array,
            required: true
        }
    }
}
</script>
